.FrontImage{
  margin-top:30px;
  max-width: 1440px;
  img{
    width: 100%;}
}
.titleWrapper{
  width: 45vw;
  margin-top:clamp(20px, 5vw, 80px);
  left:28%;
  position: absolute;
  color:white;
}
.title{
  width: 500px;
  margin-left: auto;
  margin-right: auto;
  font-style: normal;
  font-weight: 500;
  font-size: 70px;
  letter-spacing: 0.1em;
}
.description{
  margin:10px auto;
  font-size: 25px;
  font-style: normal;
  font-weight: 300;
  text-align: center;
  width: 390px;
}
.Cart{
  display:flex;
  max-width:1063px;
  margin:60px auto;
  img{
    margin:0 30px;
  }
  p{
    margin:0 30px;
    max-width: 536px;
    max-height: 472px;
    font-size: 3vh;
  }
}
.WorkshopsDesc{
  margin:100px 112px 60px;
  width: 784px;
  height: 83px;
  font-size: 3vw;
  line-height: 54px;
  letter-spacing: 0.1em;
}
.offerTitle{
  font-size: 3rem;
  width: 50px;
  height:100px;
}
.offerCart{
  display:flex;
  max-width:1063px;
  margin:60px auto;

  .offer{
    width:50%;
    margin-left:10px;
  }
  img{
    margin:0 30px;
  }
  
  p{
    margin:50px 0px;
    max-width: 566px;
    max-height: 472px;
    font-size: 3vh;
  }
  .price{
    font-size: 24px;
    margin-right: 30px;
  }
  button{
    width: 216px;
    height: 50px;
    background: rgba(87, 149, 0, 0.42);
    border:none;
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 300;
    font-size: 19px;
    line-height: 28px;
    margin-bottom: 10px;
    margin-right: 10px;
    cursor: pointer;
    &:hover{
      cursor: pointer;
      color:rgb(95, 93, 93);
      background: rgba(104, 177, 2, 0.42);
    }
  }
}